/* Reset styles */

* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  color: inherit;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

nav ul {
  list-style: none;
}

img {
  vertical-align: top;
}

nav,
footer,
header,
aside,
label {
  display: block;
}

input,
button,
textarea {
  font-family: inherit;
  color: inherit;
}

::placeholder {
  color: inherit;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

.hide {
  opacity: 0;
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #ffd200;
  z-index: 100;
  display: grid;
  place-content: center;
}

.loader {
  display: grid;
  place-content: center;
  background-image: url("../img/preloader.svg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  width: 125px;
  height: 125px;
  animation: load 1.3s linear infinite;
}

@keyframes load {
  100% {
    transform: rotate(360deg);
  }
}

/* global styles*/

body {
  font-family: "Nunito", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  color: #232323;
  background-color: #ffd200;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1215px;
  padding: 0 15px;
}

/* swiper-slider */

.swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-pointer-events {
  touch-action: pan-y;
}

.swiper-pointer-events.swiper-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */

.swiper-autoheight,
.swiper-autoheight .swiper-slide {
  height: auto;
}

.swiper-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

.swiper-backface-hidden .swiper-slide {
  transform: translateZ(0);
  backface-visibility: hidden;
}

/* 3D Effects */

.swiper-3d,
.swiper-3d.swiper-css-mode .swiper-wrapper {
  perspective: 1200px;
}

.swiper-3d .swiper-wrapper,
.swiper-3d .swiper-slide,
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom,
.swiper-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}

.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}

.swiper-3d .swiper-slide-shadow {
  background: rgba(0, 0, 0, 0.15);
}

.swiper-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */

.swiper-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}

.swiper-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}

.swiper-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-horizontal.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-vertical.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

.swiper-centered > .swiper-wrapper::before {
  content: "";
  flex-shrink: 0;
  order: 9999;
}

.swiper-centered.swiper-horizontal > .swiper-wrapper > .swiper-slide:first-child {
  margin-inline-start: var(--swiper-centered-offset-before);
}

.swiper-centered.swiper-horizontal > .swiper-wrapper::before {
  height: 100%;
  width: var(--swiper-centered-offset-after);
}

.swiper-centered.swiper-vertical > .swiper-wrapper > .swiper-slide:first-child {
  margin-block-start: var(--swiper-centered-offset-before);
}

.swiper-centered.swiper-vertical > .swiper-wrapper::before {
  width: 100%;
  height: var(--swiper-centered-offset-after);
}

.swiper-centered > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: center center;
}

.phone {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 10px;
}

.phone:hover .phone__svg {
  animation: dialing 1s linear 0.3s infinite;
}

.phone__svg {
  width: 24px;
  height: 24px;
  fill: #232323;
  transform: scale(1);
}

.phone__number {
  font-weight: 700;
  font-size: clamp(1rem, 0.925rem + 0.375vw, 1.375rem);
  color: #232323;
}

.social {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 10px;
}

.social:hover .social__svg {
  animation: dialing 1s linear 0.3s infinite;
}

.social__svg-box {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  background: radial-gradient(230.93% 230.93% at 102.01% 2.03%, rgb(249, 237, 50) 0%, rgb(238, 42, 123) 36.0000014305%, rgb(210, 42, 138) 43.9999997616%, rgb(139, 42, 178) 60.0000023842%, rgb(27, 42, 240) 82.9999983311%, rgb(0, 42, 255) 87.9999995232%);
}

.social__svg {
  width: 20px;
  height: 20px;
  fill: #fff;
}

.social__text {
  max-width: 127px;
  font-style: italic;
  font-weight: 300;
  font-size: clamp(1rem, 0.975rem + 0.125vw, 1.125rem);
  line-height: 1.11111;
  color: #232323;
}

@keyframes dialing {
  0% {
    transform: scale(1.2);
  }

  25% {
    transform: scale(1.3);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.burger {
  display: none;
  width: 24px;
  height: 16px;
  position: relative;
  transition: all 0.3s;
  z-index: 10;
}

.burger:hover .burger__span {
  background-color: #e51d82;
}

.burger__span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2.2px;
  border-radius: 1px;
  background-color: #ff4ca6;
  transition: all 0.3s;
}

.burger__span-one {
  top: 0;
}

.burger__span-two {
  top: 50%;
}

.burger__span-three {
  top: 100%;
}

.button {
  border-radius: 90px 0 180px 90px;
  padding: 16px 37px;
  box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.15);
  background: #ff4ca6;
  font-weight: 700;
  font-size: clamp(0.75rem, 0.625rem + 0.625vw, 1.375rem);
  color: #fff;
  transition: all 0.3s;
}

.button:hover {
  background: #e51d82;
}

.small-button {
  padding: 12px 26px 12px 26px;
  border-radius: 90px 0 180px 90px;
  font-weight: 700;
  font-size: clamp(0.75rem, 0.65rem + 0.5vw, 1.25rem);
  color: #fff;
  background: #ff4ca6;
  transition: all 0.3s;
}

.small-button:hover {
  background: #e51d82;
  letter-spacing: 1px;
}

/* Header styles */

.header {
  width: 100%;
  padding: 20px 0 33px 0;
}

.header__nav-box {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 30px;
}

.nav__list {
  padding-bottom: 10px;
  border-bottom: 1px solid #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}

.nav__list-clone {
  display: none;
}

.nav__list-right {
  justify-content: flex-end;
}

.nav__link {
  font-weight: 700;
  font-size: 21px;
  color: #232323;
  transition: all 0.3s;
}

.nav__link:hover {
  color: #b437e5;
}

.header__logo {
  margin-top: -15px;
}

.header__logo-clone {
  display: none;
}

.header__contacts {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 38px;
  margin-top: -74px;
}

.header__button {
  margin-left: auto;
}

/* modals styles*/

.modal-window {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding: 15px;
  z-index: -50;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s;
  background: rgba(0, 0, 0, 0.5);
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-window--visible {
  z-index: 50;
  opacity: 1;
  pointer-events: all;
}

.footer {
  width: 100%;
}

.footer__inner {
  width: 100%;
  padding: 30px 0 30px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
  border: 1px solid red;
}

.footer__column {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}

.privacy-policy__text {
  display: block;
  font-style: italic;
  font-size: clamp(0.75rem, 0.65rem + 0.5vw, 1.25rem);
  font-weight: 300;
  line-height: 1.2;
  color: #232323;
}

@media (max-width: 1200px) {
  .button {
    padding: 14px 34px;
  }

  .small-button {
    padding: 10px 24px;
  }
}

@media (max-width: 1100px) {
  .nav {
    gap: 20px;
  }

  .nav__list {
    border-bottom: none;
    gap: 20px;
  }

  .header__logo {
    order: -1;
    margin-right: auto;
  }

  .header__contacts {
    justify-content: flex-end;
    margin-top: -54px;
  }

  .header__button {
    margin-left: 0;
  }

  .nav__link {
    font-size: 18px;
  }
}

@media (max-width: 850px) {
  .burger {
    display: block;
  }

  .untranslate {
    transform: translateX(0);
  }

  .span-one-transform {
    top: 50%;
    transform: rotate(45deg);
    transition: all 0.3s;
  }

  .span-two-transform {
    transform: rotate(-45deg);
    transition: all 0.3s;
  }

  .span-three-transform {
    display: none;
  }

  .header {
    padding: 0 0 15px 0;
  }

  .nav {
    position: fixed;
    top: 0;
    right: 0;
    transition: 0.7s;
    width: 100vw;
    height: 100dvh;
    flex-direction: column;
    justify-content: flex-start;
    translate: 100% 0 0;
    padding: 100px 0 0 0;
    background-color: #ffd200;
    z-index: 5;
    overflow: hidden;
  }

  .untranslate {
    translate: 0 0 0;
  }

  .nav__list {
    display: none;
  }

  .nav__list-clone {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    gap: 30px;
  }

  .nav__item {
    opacity: 0;
    transition: all 0.3s;
    translate: 0 200px 0;
  }

  .nav__item:nth-child(1) {
    transition-delay: 0.7s;
  }

  .nav__item:nth-child(2) {
    transition-delay: 0.8s;
  }

  .nav__item:nth-child(3) {
    transition-delay: 0.9s;
  }

  .nav__item:nth-child(4) {
    transition-delay: 1s;
  }

  .nav__item:nth-child(5) {
    transition-delay: 1.1s;
  }

  .nav__item:nth-child(6) {
    transition-delay: 1.2s;
  }

  .nav__item--animate {
    translate: 0 0 0;
    opacity: 1;
  }

  .nav__link {
    transition: all 0.3s;
  }

  .nav__link:hover {
    color: #b437e5;
  }

  .header__contacts {
    justify-content: flex-start;
    margin-top: 0;
  }

  .header__logo {
    display: none;
  }

  .header__logo-clone {
    display: block;
  }

  .social__svg-box {
    width: 30px;
    height: 30px;
  }

  .social__svg {
    width: 18px;
    height: 18px;
    fill: #fff;
  }

  .footer__inner {
    flex-direction: column;
  }
}

@media (max-width: 768px) {
  .button {
    padding: 10px 30px;
  }

  .small-button {
    padding: 8px 22px;
  }

  .header__contacts {
    gap: 16px;
  }

  .social__text {
    max-width: 100px;
  }
}

@media (max-width: 600px) {
  .header__contacts {
    flex-wrap: wrap;
  }

  .header__button {
    margin-left: 0;
  }
}

@media (max-width: 480px) {
  .button {
    padding: 8px 26px;
  }

  .small-button {
    padding: 6px 18px;
  }
}